.overlay {
	color: inherit;
	background-color: inherit;
	font-family: inherit;
}
.tooltip {
	position: absolute;
	left: 0;
	top: 0;
	opacity: 0;
	max-width: 300px;
	background-color: #fff;
	padding: 10px 15px;
	border-radius: 2px;
	transition: opacity 150ms ease-in-out;
	z-index: 1300;
	box-shadow: 1px 1px 11px rgba(0, 0, 0, 0.10980392156862745);
}
.dark {
	background-color: #1b1c1c;
	border: 1px solid #4d4d4d;
	color: rgba(255, 255, 255, 0.85);
}
.show {
	opacity: 1;
}

.tooltip[data-popping-placement^='top'] > [data-popping-arrow='true'] {
	bottom: -5px;
	box-shadow: 4px 2px 5px rgba(0, 0, 0, 0.05);
}

.tooltip[data-popping-placement^='bottom'] > [data-popping-arrow='true'] {
	top: -5px;
	box-shadow: -4px -2px 5px rgba(0, 0, 0, 0.05);
}

.tooltip[data-popping-placement^='right'] > [data-popping-arrow='true'] {
	left: -5px;
	box-shadow: -4px 2px 5px rgba(0, 0, 0, 0.05);
}

.tooltip[data-popping-placement^='left'] > [data-popping-arrow='true'] {
	right: -5px;
	box-shadow: 4px -2px 5px rgba(0, 0, 0, 0.05);
}

.tooltip > [data-popping-arrow='true'] {
	display: block;
	width: 12px;
	height: 12px;
	position: absolute;
	z-index: -1;
	background-color: #fff;
}

.tooltip.dark > [data-popping-arrow='true'] {
	background-color: #1b1c1c;
}
